/* top nav */
.top_nav {
  height: 8vh;
  background-color: rgb(223, 223, 223);
  --bs-bg-opacity: 1;
  font-weight: 700;
  font-style: italic;
  z-index: 2;
}

.top_nav .container {
  max-width: 100vw;
  margin: 0 5vw;
}

.top_nav .main_tag {
  font-size: 2.7vh;
  margin-right: 5vh;
}

.top_nav .container .navbar-collapse a {
  font-size: 2vh;
  margin: 0 0.9vw;
}

/* side nav */
.side_nav {
  min-height: 100vh;
  width: 15vw;
  background-color: #4b5161;
  position: fixed;
  color: white;
  top: -0vh;
  padding-top: 4vh;
  padding-left: 1vw;
  text-align: left;
}

.side_main_title {
  margin: 0 0 5vh -2vw;
  text-align: center;
  font-size: 1.6em;
  font-weight: 700;
  font-style: italic;
}

.title {
  font-size: 1em;
  margin: 0.5em 0;
  border-left: 0.2em white solid;
  padding-left: 0.5em;
}

.title1 {
  margin: 0;
  font-size: 1.4em;
  font-weight: 600;
}
.title2 {
  font-size: 1.2em;
  margin-left: 1vw;
}
.title3,
.title4:hover {
  cursor: pointer;
}

.title3 {
  margin-left: 2vw;
}
.title4 {
  margin-left: 3vw;
}

.title3 button {
  background: rgb(62, 62, 62);
  /* border: black 1px solid; */
  color: white;
  /* padding: 0; */
  margin: 0;
  font: inherit;
  cursor: pointer;
  /* 기타 스타일 초기화 */
}
